.card {
  width: 150px;
  height: 150px;
  margin:3rem;
  position: relative;
  box-shadow: 4px 4px 10px 10px blue;
  transition: all .2s ease-in-out;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
}

.card > .img-container > img {
  height: 100%;
}

.card:hover {
  box-shadow: 8px 8px 20px 20px red;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.card:active {
  -webkit-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}
